import { useEffect, useState } from 'react';
import { Row, Col, Card, OverlayTrigger, Tooltip, Button } from 'react-bootstrap';
import { useSelector, useDispatch } from 'react-redux';
import PageTitle from '../../../helpers/PageTitle';
import { getAllStatesAction } from '../../../redux/actions';
import { Loading } from '../../../helpers/loader/Loading';
import Pagination from '../../../helpers/Pagination';
import { Link } from 'react-router-dom';
import AddLocationModal from './modal/AddLocationModal';
const States = () => {
    const store = useSelector((state) => state);
    const dispatch = useDispatch();
    const [search, setSearch] = useState('');
    const StatesData = store?.getAllStatesReducer?.locationData?.states;
    const createStatus = store?.createLocationReducer?.locationData?.status;
    const StatesLoading = store?.getAllStatesReducer?.loading;

    const TotalRecords = store?.getAllStatesReducer?.locationData?.states?.length || 0;
    const [pageIndex, setPageIndex] = useState(1);
    const [pageSize, setPageSize] = useState(20);
    const [totalPages, setTotalPages] = useState(Math.ceil(TotalRecords / pageSize));

    const [locationModal, setLocationModal] = useState({
        type: "",
        data: null,
        isVisible: false,
    });

    const getStatesApi = () => dispatch(getAllStatesAction({ limit: pageSize, page: pageIndex, search: search }));

    useEffect(() => {
        setTotalPages(Math.ceil(TotalRecords / pageSize));
    }, [TotalRecords, pageSize]);

    useEffect(() => {
        if (createStatus === 200) {
            getStatesApi();
        }
        getStatesApi();
    }, [dispatch, createStatus, pageIndex, pageSize, search]);

    const handleLocationModal = (type, data = null) => {
        setLocationModal({ type, data, isVisible: true });
    };
    return (
        <>
            <PageTitle
                breadCrumbItems={[
                    { label: 'Settings', path: '/bmg/location' },
                    {
                        label: 'Location',
                        path: '/bmg/location',
                        active: true,
                    },
                ]}
                title={'Location'}
            />
            <Row>
                <Col xs={12}>
                    <Card
                        style={{
                            boxShadow:
                                'rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset',
                        }}>
                        <Card.Body className="text-center">
                            <div className="d-flex justify-content-between align-items-center mb-3">
                                <span className="px-3 py-1 bg-dark text-light rounded">
                                    Total States: {TotalRecords || 0}
                                </span>
                                <div className="d-flex">
                                    <input
                                        type="text"
                                        className="form-control w-auto me-1"
                                        placeholder="Search..."
                                        value={search}
                                        onChange={(e) => setSearch(e.target.value)}
                                    />
                                    {search && (
                                        <i
                                            className="mdi mdi-backspace-outline text-danger fs-3 me-2"
                                            onClick={() => setSearch("")}
                                            style={{ cursor: "pointer" }}
                                        ></i>
                                    )}
                                    <Button variant="success" style={{
                                        outline: 'none',
                                        boxShadow: 'none',
                                    }} onClick={() => handleLocationModal("Add", null)}>
                                        <i className="mdi mdi-plus-circle"></i> Add
                                    </Button>
                                </div>
                                {/* <Button variant="danger" className="ms-2" style={{
                                    outline: 'none',
                                    boxShadow: 'none',
                                }} onClick={() => handleLocationModal("Edit", StatesData)}>
                                    <i className="mdi mdi-square-edit-outline"></i> Edit
                                </Button> */}
                            </div>

                            {StatesLoading ? (
                                <>
                                    <Loading />
                                </>
                            ) : (
                                <>
                                    {StatesData && StatesData?.length > 0 ? (
                                        <>
                                            <div className="table-responsive">
                                                <table className="table text-start table-striped bg-white ">
                                                    <thead>
                                                        <tr className="text-nowrap">
                                                            <th scope="col">
                                                                <i className="mdi mdi-merge"></i>
                                                            </th>
                                                            <th scope="col">State</th>
                                                            <th scope="col">Country Code</th>
                                                            <th scope="col">Country ID #</th>
                                                            <th scope="col">State Code</th>
                                                            <th scope="col">State ID #</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {StatesData?.map((data, index) => (
                                                            <tr key={index} className="text-dark fw-bold text-nowrap">
                                                                <th scope="row">{(pageIndex - 1) * pageSize + index + 1}</th>
                                                                <td className="text-uppercase fw-bold">
                                                                    {data?.name ? (
                                                                        <OverlayTrigger
                                                                            placement="left"
                                                                            overlay={
                                                                                <Tooltip id="overlay-example">
                                                                                    View Detail's
                                                                                </Tooltip>
                                                                            }>
                                                                            <Link
                                                                                style={{
                                                                                    cursor: 'pointer',
                                                                                    // color: 'crimson',
                                                                                }}
                                                                                // onMouseOver={(e) =>
                                                                                //     (e.target.style.color =
                                                                                //         'rgb(10 207 151)')
                                                                                // }
                                                                                // onMouseOut={(e) =>
                                                                                //     (e.target.style.color = 'crimson')
                                                                                // }
                                                                                to={`/bmg/cities/${data?.id}`}
                                                                                state={data}>
                                                                                {data?.name}
                                                                            </Link>
                                                                        </OverlayTrigger>
                                                                    ) : (
                                                                        <span className="d-flex justify-content-center">
                                                                            N/A
                                                                        </span>
                                                                    )}
                                                                </td>
                                                                <td className="fw-bold ">
                                                                    {data?.country_code ? (
                                                                        <span>{data?.country_code} </span>
                                                                    ) : (
                                                                        <span className="d-flex justify-content-center">
                                                                            N/A
                                                                        </span>
                                                                    )}
                                                                </td>
                                                                <td className="fw-bold ">
                                                                    {data?.country_id ? (
                                                                        <span>{data?.country_id} </span>
                                                                    ) : (
                                                                        <span className="d-flex justify-content-center">
                                                                            N/A
                                                                        </span>
                                                                    )}
                                                                </td>
                                                                <td className="fw-bold">
                                                                    {data?.state_code ? (
                                                                        <span>{data?.state_code} </span>
                                                                    ) : (
                                                                        <span className="d-flex justify-content-center">
                                                                            N/A
                                                                        </span>
                                                                    )}
                                                                </td>
                                                                <td className="fw-bold">
                                                                    {data?.id ? (
                                                                        <span>{data?.id} </span>
                                                                    ) : (
                                                                        <span className="d-flex justify-content-center">
                                                                            N/A
                                                                        </span>
                                                                    )}
                                                                </td>
                                                            </tr>
                                                        ))}
                                                    </tbody>
                                                </table>
                                            </div>
                                        </>
                                    ) : (
                                        <div
                                            className="text-center d-flex align-items-center justify-content-center"
                                            style={{ height: '30vh' }}>
                                            <code className="fs-4">No States were found.</code>
                                        </div>
                                    )}
                                </>
                            )}
                            {TotalRecords > 20 && (
                                <Pagination
                                    pageIndex={pageIndex}
                                    pageSize={pageSize}
                                    totalPages={totalPages}
                                    setPageIndex={setPageIndex}
                                    onChangePageSize={setPageSize}
                                />
                            )}
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
            <AddLocationModal
                show={locationModal.isVisible}
                hide={() => setLocationModal({ ...locationModal, isVisible: false })}
                locationData={locationModal}
            />
        </>
    );
};

export default States;
